import * as React from "react";
import { useCallback } from 'react';
import { Datagrid, TextField, FunctionField, ArrayField, ReferenceArrayField, ChipField, SingleFieldList, useListContext, ListContextProvider, ListBase, Pagination, TopToolbar, CreateButton, ExportButton } from 'react-admin';
import { Card, CardContent, Grid, Tabs, Tab } from '@material-ui/core';
import { Price } from '../equipments/equipmentList';




const processingStatus = [
    { id: '1', name: 'A retraiter' },
    { id: '2', name: 'En attente de relecture' },
    { id: '3', name: 'Bloqu' },
];

const otherStatus = [
    { id: '1', name: 'Rserv' },
    { id: '2', name: 'Vendu par Vesto' },
    { id: '3', name: 'Vendu hors Vesto' },
];

const tabs = [ processingStatus, otherStatus ]

const TabbedDatagrid = (props) => {
    const listContext = useListContext();
    const { filterValues, setFilters, displayedFilters } = listContext;
    const handleChange = useCallback(
        (event, value) => {
            setFilters &&
                setFilters(
                    { ...filterValues, 'Statut': value },
                    displayedFilters
                );
        },
        [displayedFilters, filterValues, setFilters]
    );

    return (
        <Grid container spacing={4}>
            <Grid item xs={3}>
            {tabs.map(choices => (
                <Grid item xs={12}>
                <Card>
                <CardContent>
                <Tabs
                    orientation="vertical"
                    centered
                    value={filterValues['Statut']}
                    indicatorColor="primary"
                    textColor="primary"
                    onChange={handleChange}
                >
                    {choices.map(choice => (
                        <Tab
                            key={choice.id}
                            label={choice.name}
                            value={choice.name}
                        />
                    ))}
                </Tabs>
                 </CardContent>
                 </Card>
                 <br/>
                 </Grid>))}
            </Grid>
            <Grid item xs={9}>
                <Card>
                    <CardContent>
                        <ListContextProvider
                            value={{ ...listContext }}
                        >
                            <Datagrid {...props} rowClick="edit">
                                <TextField source="Type" />
                                <TextField source="Marque" />
                                <TextField source="Modele" />
                                <ArrayField source="Owner"><SingleFieldList><ChipField source="name" /></SingleFieldList></ArrayField>
                                <FunctionField label="Prix HT" render={record => <Price>{record['Prix HT']}&nbsp;</Price>}/>
                                <ReferenceArrayField source='Reconditionneur' reference='reconditionneurs' >
                                    <SingleFieldList>
                                        <ChipField source='Nom'/>
                                    </SingleFieldList>
                                </ReferenceArrayField>
                                <ChipField source='Source'/>
                                
                            </Datagrid>
                            <Pagination />
                        </ListContextProvider>
                    </CardContent>
                 </Card>
            </Grid>
        </Grid>
    );
}





export const TabbedEquipmentProcessingList = props => (
    <ListBase
        {...props}
        filterDefaultValues={{ 'Statut': 'A retraiter' }}
        perPage={10}
    >
        <TopToolbar>
            <CreateButton basePath="/retraitement_machines" />
            <ExportButton />
        </TopToolbar>
        <TabbedDatagrid />
    </ListBase>
);