
import { useGetOne, useGetMany, useUpdateMany, useNotify, useRefresh } from 'react-admin';
import { Button } from '@material-ui/core';
import CircularProgress from '@material-ui/core/CircularProgress';
import { useState } from 'react';



export const CreateQuoteButton = ({ documentRecord, documentVariant = 'devis', deposit=0 }) => {
    const [loading, setLoading] = useState(false)
    const notify = useNotify();
    const refresh = useRefresh();
    const clientId = documentRecord && documentRecord["Client"] && documentRecord["Client"][0]
    console.log('record devis', documentRecord)


    let client = useGetOne('clients', clientId)

    let productToFetch = documentVariant === 'proforma' ? (documentRecord["Machines proforma"] || []) : (documentRecord["Machines devis"] || [])
    let products = useGetMany('vesto_stock', productToFetch)
    let lrms = useGetMany('vesto_stock', documentRecord["Livraison"] || [])

    const [updateMany] = useUpdateMany('vesto_stock')


    const createQuote = (documentRecord) => {
        console.log('in clic', client, products, lrms, documentVariant)
        notify('Pensez bien  enregistrer les informations avant de crer le document', 'info', {}, false);


        let productsData = products.data || []
        let lrmsData = lrms.data || []
        productsData = (!documentVariant === 'proforma') ? productsData.concat(lrmsData) : productsData


        //var productsList = []
        var numberList = ''
        var typeList = ''
        var modelList = ''
        var alimentationList = ''
        var priceList = ''
        var dimensionList = ''

        //get info from product
        let productsIds = []
        console.log('productsData', productsData)
        productsData.forEach(product => {

            // If product is already booked, print a warning message
            if (product['Statut'].includes("Reserv") && documentVariant==='devis') {
                notify('ATTENTION: La machine ' + product['Type'] + ' ' + product['Marque'] + ' est dj rserve. Elle a quand mme t ajoute au devis.', 'warning')
            }
            if (product['Statut'].includes("Vendu") || product['Statut'].includes("Livraison")) {
                // If product is already sold, print a warning message
                if (product['Statut'].includes("Vendu") && documentVariant==='devis') {
                    notify('ATTENTION: La machine ' + product['Type'] + ' ' + product['Marque'] + ' est vendue. Elle ne peut pas tre ajoute au devis.', 'warning')

                }
            }
            if (!product['Statut'].includes("Vendu")) {
                //productsList.push({ 'number': '1', 'type': product['Type'], 'model': product['Marque'] + ' - ' + product['Modele'], 'price': product['Prix HT'] });
                numberList += '1,';
                typeList += product['Type'] + ',';
                modelList += ((product['Marque'] || '') + (product['Marque']  && product['Modele'] ? ' - ' : '') + (product['Modele'] || '')) + ',';
                alimentationList += (product['Alimentation'] ? (product['Alimentation'][0] || '') : '') + ',';
                dimensionList += (product['Dimensions'] || '') + ',';
                priceList += product['Prix HT'] + ',';
                if (!product['Statut'].includes("Livraison")) {
                    productsIds.push(product.id)
                }
            }

            //book equiment if it's a quote
            (documentVariant === 'devis') && updateMany({ payload: { ids: productsIds, data: { 'Statut': 'Rserv' } } })

        }
        )

console.log('client', client, client.data)


var quoteData = {
    'company': client.data["Entreprise"] || '',
    'city': client.data["Adresse"] || '',
    'firstname': client.data["Prnom"] || '',
    'lastname': client.data["Nom"] || '',
    'tel': client.data["Tlphone"] || '',
    'mail': client.data["email"] || '',
    'siret': client.data["SIRET"] || '',
    'numberList': numberList,
    'typeList': typeList,
    'modelList': modelList,
    'alimentationList': alimentationList,
    'dimensionList': dimensionList,
    'priceList': priceList,
    'id': documentRecord['Numro de Liste'],
    'deposit': deposit,
}

var url = documentVariant ==='devis'
    ? 'https://script.google.com/a/vesto.fr/macros/s/AKfycbzbSt4Qnx_ZVxJAZWyJbWVP1k7oH4CPu_iLCezKFrHUadmVHcj4GI1awQClqhfCLT8l/exec?'
    : document === 'proforma'
        ? 'https://script.google.com/a/vesto.fr/macros/s/AKfycbzwanhW4zjnBqO-cOnJLOekajJnyZZWaimxpxHbmcQkEW9xzxB0AZA2i23dx_VFpPKB/exec?'
        : 'https://script.google.com/a/vesto.fr/macros/s/AKfycbyh9Rdm8IO9iXXI0pVfybK1RFGUjd79sawvUzHJAUTPwCaLfii1hEFdaKV_Z1ekjfwwKg/exec?'


for (var j = 0; j < Object.keys(quoteData).length; j++) {
    url += Object.keys(quoteData)[j] + '=' + encodeURI(quoteData[Object.keys(quoteData)[j]]) + '&'
}

documentVariant === 'devis' && console.log('Les machines ont t mises en statut Rserv')
//output.markdown(url)
console.log(url)
return (url)
    }

const handleClick = async (record) => {
    console.log('in handle click')
    setLoading(true)
    if (true) {

        let url = await createQuote(record)
        setLoading(false)
        var ww = window.open(url, "_blank")
        window.close(ww)


    } else {
        notify('Manque d\'informations', 'warning', {}, false);
    }
    refresh()
}

return (
    <Button color='primary' onClick={() => handleClick(documentRecord)} variant="contained" target="_blank">{loading && <CircularProgress color="inherit" size={20} />}{documentVariant === 'facture' ? `  Gnrer une ${documentVariant}` : `  Crer un ${documentVariant}`}</Button>
)
}