const requestedProps = [
    'Type',
    'Marque',
    'Modele',
    'Prix HT',
    'Prix neuf HT',
    'Dimensions',
    'Capacit',
    'Alimentation',
    'Sous-Catgorie',
    'Etat'
]

const validateValues = (props, notify) => {
    let error = {}
    let isError = false
    props.setError('Obligatoire')
    requestedProps.forEach(prop => {
        if (!props.record[prop]) {
            isError = true
            error[prop] = 'Obligatoire'
        }
    })
    if (props.reduction > -30) {
        notify('Le prix est trop haut', 'warning', {}, false);
        return false
    }
    if (!props.record['Avis Vesto']) {
        notify('Il manque l\'avis Vesto', 'warning', {}, false);
        return false
    }
    props.setError(error)
    console.log('in validate', isError, error)
    return !isError
    
}

const replaceUrl = async (record, url, refresh) => {
    let data = equipmentTransform(record)
    await dataProvider.update('vesto_stock', {id: record['id'], data: {...data, 'Lien': url}})
    refresh()
}

export const AddOnlineShopButton = (props) => {
    const notify = useNotify();
    const refresh = useRefresh();
    let [loading, setLoading] = useState(false)
    
    const handleClick = async (props) => {
        console.log('in handle click')
        setLoading(true)
        let isValidated = validateValues(props, notify)
        if (isValidated) {
            
            let {url, clientUrl} = await addOnlineShop(props.record, notify)
            setLoading(false)
            window.open(url, "_blank")
            
            //replace url
            replaceUrl(props.record, clientUrl, refresh)
            
        } else {
            notify('Manque d\'informations', 'warning', {}, false);
        }
        refresh()
    }
    return(
        <Button {...props} color="primary" variant="contained" disabled={!props.pristine} onClick={() => handleClick(props)} >{loading && <CircularProgress color="inherit" size={20} />}  Ajouter au E-shop</Button>
    )
}