
import DeleteIcon from '@material-ui/icons/Delete';
import customDataProvider from '../dataProvider';
import { IconButton, ThemeProvider } from '@material-ui/core';
import { createMuiTheme } from '@material-ui/core/styles';

import { useRefresh } from 'react-admin';
import { red } from '@material-ui/core/colors';
import { listTransform } from '../operations/operationEdit';


const theme = createMuiTheme({
    palette: {
      primary: red,
    },
});
  


const dataProvider = customDataProvider('https://api.airtable.com/v0/app7dXu5oLwGP1OX5');



export const EquipmentQuickDeleteButton = (props) => {
    const refresh = useRefresh();

    const equipmentQuickDelete = async () => {
        props.setDeleting(true)
        if (props.var === 'proforma') {
            props.record['Machines proforma'] && props.record['Machines proforma'].splice(props.record['Machines proforma'].indexOf(props.equipmentId))
            const params = { id: props.record.id, data: listTransform({...props.record, 'Machines proforma': props.record['Machines proforma'] })}
            await dataProvider.update('listes', params)
        } else {
            props.record['Mach. possibles'] && props.record['Mach. possibles'].splice(props.record['Mach. possibles'].indexOf(props.equipmentId))
            const params = { id: props.record.id, data: {'Dtails': props.record['Dtails'], 'Type de machine': props.record['Type de machine'], 'Client': props.record['Client'], 'Mach. possibles': props.record['Mach. possibles'] }}
            await dataProvider.update('machines_desirees', params)
        }
        refresh();
        props.setDeleting(false)
    }

    return (<ThemeProvider theme={theme}><IconButton edge='start' size='medium' aria-label="delete" onClick={() => equipmentQuickDelete(props)} ><DeleteIcon /></IconButton></ThemeProvider>)
}