import {
    Typography,
    Card,
    CardContent,
    Grid,
    Stepper,
    Step,
    StepLabel,
    StepContent,
} from '@material-ui/core';
import { useGetList, FormWithRedirect, SelectInput, SelectArrayInput, TextInput, SaveButton, useNotify, required, useRefresh, useCreate } from 'react-admin';

import { users } from '../users';
import { makeStyles } from '@material-ui/core/styles';
import React, { useEffect } from 'react';
import { useForm } from 'react-final-form';
import { useState } from 'react';

import MessageIcon from '@material-ui/icons/Message';

const useStyles = makeStyles({
    stepper: {
        background: 'none',
    },
    card: {
        marginLeft: '0.3em',
        minWidth: '350px',
        maxWidth: '350px',
        height: 'fit-content',
        overflow: 'scroll',
        background: 'none',
        maxHeight: '400px',
        boxShadow: 'none',
    },
    addCard: {
        marginLeft: '24px',
        width: '350px',
        height: 'fit-content',
    },
    title: {
        marginLeft: '24px',
        width: '350px',
        marginBottom: '20px',
    },
    grid: {
        height: 'fit-content',
        maxWidth: '350px'
    }
});


const AddCommentFormContent = ({pristine, loading, handleSubmitWithRedirect, saving, setSaved, saved}) => {
        const form = useForm()
        const handleSubmit = event => {
            handleSubmitWithRedirect(event)
            //form.change('message', '')
        }
        if (saved) {
            form.change('message', '')
            setSaved(false)
        }
        return(
                    <>
                        <CardContent >
                            <TextInput
                                source="message"
                                fullWidth
                                helperText=''
                                variant='outlined'
                                multiline
                            />
                            <SelectInput
                                source='author'
                                fullWidth
                                label='Auteur'
                                choices={users}
                                validate={required()}
                                optionText='name'
                                helperText=''
                                variant='outlined'
                            />
                            <SelectArrayInput
                                source='tagued'
                                fullWidth
                                label='A taguer'
                                choices={users}
                                optionText='name'
                                variant='outlined'
                            />
                            <SaveButton
                                handleSubmitWithRedirect={
                                    handleSubmit
                                }
                                pristine={pristine}
                                saving={saving}
                                disabled={loading}
                                label='Commenter'
                            />
                        </CardContent>
                    </>
    )
}

const AddCommentForm = (props) => {
    const refresh = useRefresh()
    const notify = useNotify()
    //
    const [create, { loading }] = useCreate('comments');
    const [saved, setSaved] = useState(false);



    const handleSubmit = async values => {
        values.recordId = props.record.id
        values.author = {id: values.author}
        values.tagued = values.tagued && values.tagued.map(user => ({id: user}))
        values.link = window.location.href
        create(
            { payload: { data: values } },
            {
                onSuccess: ({ data }) => {
                    // Update the comment form to target the newly created post
                    // Updating the ReferenceInput value will force it to reload the available posts
                    refresh();
                    setSaved(true)
                    //form.change('message', '')
                },
                onFailure: ({ error }) => {
                    notify(error.message, 'error');
                    console.log(error)
                }
            }
        );
    };

    return(
        <FormWithRedirect
                resource="comments"
                save={handleSubmit}
                render={({
                    handleSubmitWithRedirect,
                    pristine,
                    saving
                }) => (
                    <AddCommentFormContent handleSubmitWithRedirect={handleSubmitWithRedirect} pristine={pristine} saving={saving} loading={loading} saved={saved} setSaved={setSaved}/>
                )
                }
            />
    )
}


export const Comments = ({record}) => {
    const classes = useStyles()

    const { data, ids } = useGetList(
        'comments',
        { page: 1, perPage: 100 },
        { field: 'date', order: 'asc' },
        { recordId: record && record.id}
    );

    function updateScroll(){
        if (document.getElementById("comments-card")) {
            var element = document.getElementById("comments-card");
            element.scrollTop = element.scrollHeight;
        }
        
    }
    useEffect(() => {
        updateScroll()
    })

    return ( 
        <Grid container classes={{root: classes.grid}}>
            <Typography variant='h6' className={classes.title}>Commentaires</Typography>
        {ids.length > 0 && <Card classes={{ root: classes.card }} id='comments-card'>
            <Stepper orientation="vertical" classes={{ root: classes.stepper }}>
                {ids.map(id => {
                    return(
                    <Step
                        key={data[id].id}
                        expanded
                        active
                        completed
                    >
                        <StepLabel
                            StepIconComponent={MessageIcon}
                        >
                            {`${data[id].author.name} - ${new Date(data[id].date).toLocaleDateString('fr', {
                                'day': 'numeric',
                                'month': 'short',
                                'hour': 'numeric',
                                'minute': 'numeric',
                            })}`}
                        </StepLabel>
                        <StepContent>
                            <Comment
                                comment={data[id].message}
                                />
                        </StepContent>
                    </Step>)
                })}
            </Stepper>
            
        </Card>}
        <Card classes={{root: classes.addCard}}>
            <AddCommentForm record={record}/>
        
            </Card>
        </Grid>
    );
};

const Comment = (props) => {
    return (
        <>
            <Typography
                variant="body2"
                color="textSecondary"
                className={''}
            >
                {props.comment}
            </Typography>
        </>
    );
};
