import { SimpleForm, useRedirect, useDataProvider, ReferenceArrayInput, Toolbar, AutocompleteInput, Button as RAButton, useCreate, FormWithRedirect, ReferenceInput, Create } from 'react-admin';
import { Typography, Switch, FormGroup, FormControlLabel } from '@material-ui/core';
import { ClientForm, CompanyForm } from './generalFormTab';
import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { useStyles as formUseStyles, clientTransform, companyTransform} from './listEdit';
import { listTransform } from '../operations/operationEdit';
import SaveIcon from '@material-ui/icons/Save';


const useStyles = makeStyles(theme => ({
    switch: {
        width: '58px',
    },
}))



const ListCreateForm = props => {
    const classes = useStyles()
    const formClasses = formUseStyles()
    const [create] = useCreate('clients');
    const [createList] = useCreate('listes');
    const [ clientProps, setClientProps] = useState()
    const [ companyProps, setCompanyProps] = useState()
    const [clients, setClients] = useState([])
    const [newClient, setNewClient] = useState(true)
    const dataProvider = useDataProvider()

    
    const redirect = useRedirect();
    const getClients = async () => {
        const clients = await dataProvider.getList('clients', {'pagination': {'perPage': 1000}, 'sort': {'field': 'id', 'order': 'asc'}, 'filter': {}})
        setClients(clients)
        return clients
    }

    useEffect(() => {
        getClients()
    }, []) // eslint-disable-line react-hooks/exhaustive-deps

    const CustomToolbar = props => (
        <Toolbar {...props} classes={{toolbar: formClasses.toolbar}} alwaysEnableSaveButton>
            <RAButton variant='contained' onClick={() => (newClient && handleClientSave(props))} label='Crer' ><SaveIcon/></RAButton>
        </Toolbar>
    );

    const createListMethod = (id) => {
        createList(
            { payload: { data: {'Client': [id], 'Statut': '1. A attribuer'} } },
            {
                onSuccess: ({ data }) => {
                    console.log('success list', data)
                    redirect('/listes/' + data.id)
                },
                onFailure: ({ error }) => {
                }
            }
        )
    }

    const handleClientSave = (saveProps) => {
        const clientData = clientTransform(clientProps)
        const companyData = companyTransform(companyProps)
        const data = {...clientData, ...companyData}
        console.log('handlesubmit props', saveProps)
        create(
            { payload: { data: data } },
            {
                onSuccess: ({ data }) => {
                    createListMethod(data.id)
                },
                onFailure: ({ error }) => {
                }
            }
        );
    }

    return(
        <SimpleForm {...props} variant='outlined' toolbar={<CustomToolbar />}>
            <FormGroup row>
                <FormControlLabel control={
                    <Switch classes={{root: classes.switch}} checked={newClient} onChange={(event) => setNewClient(event.target.checked)}/>}
                    label={newClient ? 'Nouveau client' : 'Dj client'}/>
            </FormGroup><br/>
            { !newClient && <ReferenceArrayInput fullWidth source='Client' reference='clients'>
                <AutocompleteInput setFilter={() => false} choices={clients.data ? clients.data : []} optionText={(record) => (`${(record && record['Prnom']) || ''}  ${(record && record['Nom']) || ''} -  ${(record && record['Entreprise']) || ''}`)} />
            </ReferenceArrayInput>}
            { newClient && <><Typography variant='h6'>Infos Client</Typography>
            <ReferenceInput link={false} fullWidth source='Client[0]' label='' reference='clients'>
                <FormWithRedirect {...props} setClientProps={setClientProps} render={formProps => <ClientForm {...formProps}/>}/>
            </ReferenceInput>
            <br/><Typography variant='h6'>Infos Entreprise</Typography>
            <ReferenceInput link={false} fullWidth source='Client[0]' label='' reference='clients'>
                <FormWithRedirect {...props} setCompanyProps={setCompanyProps} save={(data) => console.log(data)} render={formProps => <CompanyForm {...formProps}/>}/>
            </ReferenceInput></>}
        </SimpleForm>
    )
}

export const ListCreate = props => {
    const [clientId, setClientId] = useState()
    console.log('client id', clientId)

    const getClientId = () => {
        console.log ('in get client id', clientId)
        return(clientId)
    }
    const transform = (data, clientId) => {
        console.log('client id in transform', getClientId())
        let newData = data
        if (data['Client'] || clientId) {
            newData['Client'] = data['Client'] ? [data['Client']] :  [clientId]
        } else {
            return(transform(data))
        }
        return(listTransform(newData))
    }

    return(
    <Create {...props} transform={transform}>
        <ListCreateForm setClientId={setClientId} />
    </Create>
)};