
import * as React from 'react';
import { useState, Fragment } from 'react';
import { ListItemIcon, MenuItem, Box, Typography, Tooltip, List, Collapse } from '@material-ui/core';
import {
    DashboardMenuItem,
    MenuItemLink,
    Layout,
} from 'react-admin';
import ExpandMore from '@material-ui/icons/ExpandMore';
import ClientIcon from '@material-ui/icons/Group';
import AssignmentIcon from '@material-ui/icons/Assignment';
import ViewModuleIcon from '@material-ui/icons/ViewModule';
import SearchIcon from '@material-ui/icons/Search';
import LocalShippingIcon from '@material-ui/icons/LocalShipping';
import AssignmentTurnedInIcon from '@material-ui/icons/AssignmentTurnedIn';
import EuroIcon from '@material-ui/icons/Euro';
import PeopleIcon from '@material-ui/icons/People';
import BuildIcon from '@material-ui/icons/Build';
import DescriptionIcon from '@material-ui/icons/Description';
import { makeStyles } from '@material-ui/core/styles';
import styled from 'styled-components';


const StyledMenuItem = styled(MenuItemLink)`
    font-weight: 1000;
    margin: 5px 32px;
    padding: 6px;
    padding-left: 6px!important;
    background-color: #5fd3a1;
    border-radius: 5px;
    color: white;
    justify-content: center;
    `;


const useStyles = makeStyles(theme => ({
    icon: { minWidth: theme.spacing(4), color: '#192D4B' },
    sidebarIsOpen: {
        width: 'auto',
        '& a': {
            paddingLeft: theme.spacing(4),
            transition: 'padding-left 195ms cubic-bezier(0.4, 0, 0.6, 1) 0ms',
        },
    },
    sidebarIsClosed: {
        '& a': {
            paddingLeft: theme.spacing(2),
            transition: 'padding-left 195ms cubic-bezier(0.4, 0, 0.6, 1) 0ms',
        },
    },
    category: {
        color: '#192D4B'
    }
}));

export const CustomLayout = (props) => {
    return (
        <Layout
            {...props}
            menu={Menu}
        />
    );
};


const Menu = ({ onMenuClick }) => {
    const [state, setState] = useState({
        menuOps: true,
        menuSales: true,
        menuMkp: true,
        menuAdmin: false,
    });
    //const open = useSelector((state) => state.admin.ui.sidebarOpen);
    const open = true;

    const handleToggle = (menu) => {
        setState(state => ({ ...state, [menu]: !state[menu] }));
    };

    const dense = false;

    //return(<Box mt={1}/>)

    return (
        <Box mt={1}>
            {' '}
            <DashboardMenuItem onClick={onMenuClick} sidebarIsOpen={open} />
            <SubMenu
                handleToggle={() => handleToggle('menuSales')}
                isOpen={state.menuSales}
                sidebarIsOpen={open}
                name='Sales'
                icon={<EuroIcon />}
                dense={dense}
            >
                <MenuItemLink
                    to={`/clients`}
                    primaryText={'Clients'}
                    leftIcon={<ClientIcon />}
                    onClick={onMenuClick}
                    sidebarIsOpen={open}
                    dense={dense}
                />
                <MenuItemLink
                    to={`/listes`}
                    primaryText={'Demandes'}
                    leftIcon={<AssignmentIcon />}
                    onClick={onMenuClick}
                    sidebarIsOpen={open}
                    dense={dense}
                />
                <MenuItemLink
                    to={`/vesto_stock`}
                    primaryText={'Vesto Stock'}
                    leftIcon={<ViewModuleIcon />}
                    onClick={onMenuClick}
                    sidebarIsOpen={open}
                    dense={dense}
                />
                <StyledMenuItem
                    to={`/listes/create`}
                    className={{'color': 'red'}}
                    primaryText={'Nouvelle demande'}
                    sidebarIsOpen={open}
                    dense={dense}
                />
            </SubMenu>
            <SubMenu
                handleToggle={() => handleToggle('menuOps')}
                isOpen={state.menuOps}
                sidebarIsOpen={open}
                name="Ops"
                icon={<BuildIcon />}
                dense={dense}
            >
                <MenuItemLink
                    to={`/operations`}
                    primaryText={'Oprations'}
                    leftIcon={<LocalShippingIcon />}
                    onClick={onMenuClick}
                    sidebarIsOpen={open}
                    dense={dense}
                />
                <MenuItemLink
                    to={`/sav`}
                    primaryText={'SAV'}
                    leftIcon={<BuildIcon />}
                    onClick={onMenuClick}
                    sidebarIsOpen={open}
                    dense={dense}
                />
            </SubMenu>
            <SubMenu
                handleToggle={() => handleToggle('menuMkp')}
                isOpen={state.menuMkp}
                sidebarIsOpen={open}
                name="Marketplace"
                icon={<PeopleIcon />}
                dense={dense}
            >
                <MenuItemLink
                    to={`/retraitement_machines`}
                    primaryText={'Retraitement'}
                    leftIcon={<AssignmentTurnedInIcon />}
                    onClick={onMenuClick}
                    sidebarIsOpen={open}
                    dense={dense}
                />
                <MenuItemLink
                    to={`/machines_desirees`}
                    primaryText={'Machines dsires'}
                    leftIcon={<SearchIcon />}
                    onClick={onMenuClick}
                    sidebarIsOpen={open}
                    dense={dense}
                />
                <MenuItemLink
                    to={`/reconditionneurs`}
                    primaryText={'Reconditionneurs'}
                    leftIcon={<PeopleIcon />}
                    onClick={onMenuClick}
                    sidebarIsOpen={open}
                    dense={dense}
                />
            </SubMenu>
            <SubMenu
                handleToggle={() => handleToggle('menuAdmin')}
                isOpen={state.menuAdmin}
                sidebarIsOpen={open}
                name="Admin"
                icon={<DescriptionIcon />}
                dense={dense}
            >
                <MenuItemLink
                    to={`/factures`}
                    primaryText={'Factures partenaires'}
                    leftIcon={<DescriptionIcon />}
                    onClick={onMenuClick}
                    sidebarIsOpen={open}
                    dense={dense}
                />
                <MenuItemLink
                    to={`/factures_clients`}
                    primaryText={'Factures clients'}
                    leftIcon={<DescriptionIcon />}
                    onClick={onMenuClick}
                    sidebarIsOpen={open}
                    dense={dense}
                />
            </SubMenu>
        </Box>
    );
};

const SubMenu = ({
    handleToggle,
    sidebarIsOpen,
    isOpen,
    name,
    icon,
    children,
    dense,
}) => {

    const classes = useStyles();

    const header = (
        <MenuItem dense={dense} button onClick={handleToggle} className={classes.category}>
            <ListItemIcon className={classes.icon} >
                {isOpen ? <ExpandMore /> : icon}
            </ListItemIcon>
            <Typography variant="h6" >
                {name}
            </Typography>
        </MenuItem>
    );

    return (
        <Fragment>
            {sidebarIsOpen || isOpen ? (
                header
            ) : (
                    <Tooltip title={name} placement="right">
                        {header}
                    </Tooltip>
                )}
            <Collapse in={isOpen} timeout="auto" unmountOnExit>
                <List
                    dense={dense}
                    component="div"
                    disablePadding
                    className={
                        sidebarIsOpen
                            ? classes.sidebarIsOpen
                            : classes.sidebarIsClosed
                    }
                >
                    {children}
                </List>
            </Collapse>
        </Fragment>
    );
};