import { NumberInput, useGetOne, NumberField, SimpleForm, BooleanInput, FunctionField, Edit, required, TextInput, SelectInput, Create, AutocompleteArrayInput, ReferenceArrayField, ChipField, SingleFieldList } from 'react-admin';
import { Typography, Grid } from '@material-ui/core';
import { useDataProvider, ReferenceArrayInput } from 'react-admin';
import { billsStatus } from './clientsBillList';
import { useStyles } from '../lists/listEdit';
import { useState, useEffect } from 'react';
import { CreateQuoteButton } from '../lists/createQuote';
import { Comments } from '../comments/comments';



export const Fees = (props) => {
    const classes = useStyles()

    // mettre des fields materiel ui avec props.record. ...
    return (
        <Grid {...props} container spacing={0}>
            <NumberField variant='outlined' className={classes.inlineField} fullWidth source='Frais supplmentaires' />
            <TextInput variant='outlined' className={classes.inline23Field} fullWidth source='Notes frais' />
        </Grid>)
}


const ClientsBillEditForm = (props) => {
    const classes = useStyles()
    const [amount, setAmount] = useState(props.record && props.record['Total (HT)'])
    // get related list
    let list = useGetOne('listes', props.record && props.record['Client'] && props.record['Client'][0])

    return(
        <SimpleForm {...props} redirect={false} variant='outlined'>
                <SelectInput source="Statut" choices={billsStatus} />
                <NumberInput onChange={(event) => setAmount(event.target.value)} source="Total (HT)" label='Total (HT)' formClassName={classes.inlineHalfField} fullWidth validate={required()}/>
                <FunctionField formClassName={classes.inlineHalfField} fullWidth label="Total (TTC)" render={record => `${Math.round(amount * 1.2 * 100)/100} `} />
                <FunctionField formClassName={classes.inlineHalfField} fullWidth label="Acompte (TTC)" render={record => `${Math.round(amount * 1.2 * 0.3 * 100)/100} `} />
                <BooleanInput  formClassName={classes.inlineHalfField} fullWidth source='Acompte pay'/>
                <br/>
                <ReferenceArrayField source="Client" reference="listes"  label='Demande client associe'>
                    <SingleFieldList>
                        <ChipField source='Nom'/>
                    </SingleFieldList>
                </ReferenceArrayField>
                {/*<ReferenceField link={false} formClassName={classes.inline23Field} fullWidth source='Client[0]' reference='listes'>
                    <FormWithRedirect {...props} render={formProps => <Fees {...formProps} />} />
    </ReferenceField>*/}
                <br/>
                <TextInput formClassName={classes.fullWidth} multiline fullWidth source="Notes" />
                <br/>
                { list && list.data && <CreateQuoteButton documentVariant='facture' documentRecord={list.data} deposit={props.record['Acompte pay'] ? Math.round(amount * 1.2 * 0.3 * 100)/100 : 0}/>}
                <br/>
                {/*<FileField source="Facture" src='url' title='filename' target="_blank"  fullWidth/>*/}
                {/*<Button href={`https://airtable.com/shr4koFG89mHYYMhC?prefill_Facture%20client=${props.record.id}`} formClassName={classes.inline23Field} variant='contained' color='secondary' target='_blank' >Ajouter une facture PDF</Button>*/}
        </SimpleForm>
    )
}
export const ClientsBillEdit = props => {
    return(
        <Edit {...props} aside={<Comments/>} >
            <ClientsBillEditForm />
        </Edit>
    )
};

export const ClientsBillCreate = (props) => {
    const classes = useStyles()
    const dataProvider = useDataProvider()
    const [ clients, setClients ] = useState()

    const getClients = async () => {
        const clients = await dataProvider.getList('listes', {'pagination': {'perPage': 1000}, 'sort': {'field': 'id', 'order': 'asc'}, 'filter': {}})
        setClients(clients)
        return clients
    }

    useEffect(() => {
        getClients()
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    const [amount, setAmount] = useState(0)

    const transform = (data) => {
        let newData = data
        if (data['Client']) {
            newData['Client'] = data['Client']
        }
        return(newData)
    }

    console.log('props create', props)

    return(
    <Create {...props} transform={transform}>
        <SimpleForm variant='outlined'>
            <Typography variant='h6'>Nouvelle facture</Typography>
                <SelectInput source="Statut" choices={billsStatus} formClassName={classes.inlineField} fullWidth validate={required()}/>
                <NumberInput onChange={(event) => setAmount(event.target.value)} source="Total (HT)" label='Total (HT)' formClassName={classes.inlineField} fullWidth validate={required()}/>
                <FunctionField formClassName={classes.inlineField} fullWidth label="Total (TTC)" render={record => `${Math.round(amount * 1.2 * 100)/100} `} />
                <ReferenceArrayInput formClassName={classes.inline23Field} fullWidth source='Client' label='Demande client associe' reference='listes'>
                    <AutocompleteArrayInput setFilter={() => false} choices={(clients && clients.data) || []} optionText={(record) => (`${(record && record['Nom']) || ''}`)} />
                </ReferenceArrayInput>
                <br/>
                <TextInput multiline formClassName={classes.fullWidth} fullWidth source="Notes" />
        </SimpleForm>
    </Create>
    )
}