const dataProvider = customDataProvider('https://api.airtable.com/v0/app7dXu5oLwGP1OX5');

export const validateValues = (props) => {
    const requestedProps = [
        'Type',
        'Prix HT',
        'Capacit',
        'Alimentation',
        'Sous-Catgorie',
        'Dimensions',
        'Etat'
    ]
    let error = {}
    let isError = false
    requestedProps.forEach(prop => {
        if (!props[prop]) {
            isError = true
            error[prop] = 'Obligatoire'
        }
    })
    return isError ? error : false
}

export const CustomImageField = props => {
    const {
        className,
        emptyText,
        record,
        source,
        source1,
        source2,
        src,
        title
    } = props;
    const sourceValue0 = record && record[source];
    const sourceValue1 = record && record[source1];
    const sourceValue2 = record && record[source2];
    const sourceValue = sourceValue0 ? sourceValue0.concat(sourceValue1 ? sourceValue1 : []).concat(sourceValue2 ? sourceValue2 : []) : []
    const classes = useStyles(props);
    if (!sourceValue) {
        return emptyText ? (
            <Typography
                component="span"
                variant="body2"
                className={className}

            >
                {emptyText}
            </Typography>
        ) : (
            <div className={className}  />
        );
    }

    if (Array.isArray(sourceValue)) {
        return (
            <div style={{'display': 'flex', 'flex-wrap': 'wrap'}}
            >
                {sourceValue.map((file, index) => {
                    const fileTitleValue = file[title] || title;
                    const srcValue = file[src];

                    return (
                        <div key={index}>
                            <a href={srcValue} rel='noreferrer' target='_blank'>
                            <img
                                alt={fileTitleValue}
                                title={fileTitleValue}
                                src={srcValue}
                                className={classes.image}
                            />
                            </a>
                        </div>
                    );
                })}
            </div>
        );
    }

    const titleValue = record ? record[title] : title;

    return (
        <div className={className} >
            <a href={record && record[source][src]} rel='noreferrer' target='_blank'>
            <img
                title={titleValue}
                alt={titleValue}
                src={record && record[source][src]}
                className={classes.image}
            />
            </a>
        </div>
        
    );
};

export const EquipmentProcessingForm = props => {

    const getCat = async () => {
        const subCategories = await dataProvider.getList('categories', {'pagination': {}, 'sort': {'field': 'Catgorie', 'order': 'asc'}, 'filter': {}})
        setSubCategories(subCategories)
        return subCategories
    }

    const [subCategories, setSubCategories] = useState([])
    useEffect(() => {
        getCat()
    }, [])

    const classes = useStyles();
    const [price, setPrice] = useState(props.record['Prix HT'])
    const [initialPrice, setInitialPrice] = useState(props.record['Prix neuf HT'])

    const onChangePrice = (event) => {
        setPrice(event.target.value)
    }

    const onChangeInitialPrice = (event) => {
        setInitialPrice(event.target.value)
    }

    let reduction = Math.round((price - initialPrice)/initialPrice*100)
    let id = props.record['id']

    return(
        <SimpleForm variant='outlined' warnWhenUnsavedChanges {...props} toolbar={<EquipmentProcessingActions {...props} setError={props.setError} reduction={reduction}/>}>
            <TextInput source="Type" fullWidth formClassName={classes.inline23Field} error={props.error && props.error['Type']} helperText={props.error && props.error['Type']}/>
            <ReferenceArrayField source='Reconditionneur' reference='reconditionneurs' formClassName={classes.inline6Field} >
                <SingleFieldList>
                    <ChipField source='Nom'/>
                </SingleFieldList>
            </ReferenceArrayField>
            <TextField source='Source' formClassName={classes.inline6Field} />
            <SelectInput source='Statut' formClassName={classes.inlineField} choices={equipmentStatusChoices} optionText='name'/>
            <SelectInput formClassName={classes.inlineHalfField} source="Owner name" label='Owner' optionText='name' optionValue='name' choices={users} />



            <Typography variant='h6'>Caractristiques</Typography>
            <TextInput source="Marque" formClassName={classes.inlineField} error={props.error && props.error['Marque']} helperText={props.error && props.error['Marque']}/>
            <TextInput source="Modele" formClassName={classes.inlineField} error={props.error && props.error['Modele']} helperText={props.error && props.error['Modele']}/>
            <TextInput source="Anne" formClassName={classes.inlineField}/>

            <NumberInput label='Prix HT' source="Prix HT" value={price} onChange={onChangePrice} formClassName={classes.inlineField} error={props.error && props.error['Prix HT']} helperText={props.error && props.error['Prix HT']} />
            <NumberInput label='Prix neuf HT' source="Prix neuf HT" value={initialPrice} onChange={onChangeInitialPrice} formClassName={classes.inlineField} error={props.error && props.error['Prix neuf HT']} helperText={props.error && props.error['Prix neuf HT']}/>
            <FunctionField className={reduction <= -30 ? classes.green : classes.red} label='Pourcentage de rduction' render={record => price && initialPrice && `${reduction} %`} formClassName={classes.inlineField} />

            
            <TextInput source="Dimensions" formClassName={classes.inlineField} error={props.error && props.error['Dimensions']} helperText={props.error && props.error['Dimensions']}/>
            
            <TextInput source="Capacit" formClassName={classes.inlineField} error={props.error && props.error['Capacit']} helperText={props.error && props.error['Capacit']} />
            <SelectArrayInput source="Alimentation" choices={alimentationChoices} optionText='name' formClassName={classes.inlineField} error={props.error && props.error['Alimentation']} helperText={props.error && props.error['Alimentation']} />
            <TextInput source="Puissance" formClassName={classes.inlineField}/>
            <TextInput source="Poids" formClassName={classes.inlineField}/>
            
            <SelectInput formClassName={classes.inlineField} source="Etat" choices={etatChoices} optionText='name' error={props.error && props.error['Etat']} helperText={props.error && props.error['Etat']}/>
            <Typography variant='h6'>Catgories et Notes</Typography>
            <ReferenceArrayInput source='Sous-Catgorie' reference='categories' formClassName={classes.inlineHalfField}>
                <AutocompleteArrayInput setFilter={null} fullWidth choices={subCategories.data ? subCategories.data : []} optionText={(record) => (`${record && record['Nom']}  -  ${record && record['Catgorie']}`)} formClassName={classes.inlineField} meta={{'touched': props.error && props.error['Sous-Catgorie'], 'error': props.error && props.error['Sous-Catgorie'], 'submitError': props.error && props.error['Sous-Catgorie']}} helperText={props.error && props.error['Sous-Catgorie']}/>
            </ReferenceArrayInput>
            <Typography variant='h6'> </Typography>

            <TextInput multiline fullWidth source="Avis Vesto" formClassName={classes.inlineHalfField} />
            <TextInput multiline fullWidth source="Notes" formClassName={classes.inlineHalfField}/>
    
            <Typography variant='h6'>Documents</Typography>
            {/*<ImageField source="Photo" src='url' formClassName={classes.inlineHalfField}/>
            <ImageField source="Photo plaque" src='url' formClassName={classes.inlineHalfField}/>*/}
            <CustomImageField addLabel={true} source="Photo" source1="Photo 2 (TECH)" source2="Photo 3 (TECH)" src='url' formClassName={classes.inlineHalfField}/>
            <CustomImageField addLabel={true} source="Photo plaque" src='url' formClassName={classes.inlineHalfField}/>

        
            {/* <FileInput source="Fiche technique" label='Fiche technique' multiple placeholder={<p>Ajouter un document</p>}>
                <FileField source="url" title='filename' target="_blank" />
            </FileInput> */}
            <FileField source="Fiche technique" src='url' title='filename' target="_blank"  fullWidth/>
            <Button href={`https://airtable.com/shrgizktKbhXC5z8J?prefill_Machine=${id}`} formClassName={classes.inline23Field} variant='contained' color='secondary' target='_blank' >Ajouter une Fiche Technique</Button>
            
        </SimpleForm>
    )
}


