export const EquipmentProcessingEdit = props => {

    const notify = useNotify();
    //let previousState = props.record['Statut']
    const [error, setError] = useState(false)

    const validateAvailableData = (data) => {
        if (data['Statut'] === 'Disponible' ) {
            var validateError = validateValues(data)
            if (validateError) {
                setError(validateError)
                data['Statut'] = 'A retraiter'
                console.log('has error', error, validateError)
                notify('Pas assez d\'infos pour passer en disponible', 'warning', {}, false);
            } else {
                setError({})
            }
            return equipmentTransform(data);
        }
        return equipmentTransform(data)
    }
    
    
    return(
    <Edit {...props} undoable={false} transform={validateAvailableData} aside={<Comments/>} >
        <EquipmentProcessingForm error={error} setError={setError}/>
    </Edit>
    )};