import * as React from "react";
import { Admin, ListGuesser, Resource, EditGuesser } from 'react-admin';
import customDataProvider from './dataProvider';
import { CustomClientList, ClientEdit } from './clients';
import { TabbedListList } from './lists/listesList';
import { ListCreate } from './lists/listCreate';
import { ListeEdit } from './lists/listEdit';
import { TabbedEquipmentProcessingList } from './equipmentProcessing/equipmentProcessingList'
import { EquipmentProcessingEdit } from './equipmentProcessing/equipmentProcessingEdit';
import { EquipmentProcessingCreate } from './equipmentProcessing/equipmentProcessingCreate';
import { CustomLayout } from './Layout'
import { createMuiTheme } from '@material-ui/core/styles';
import { TabbedOperationsList } from './operations/operationsList';
import { TabbedSAVList } from './operations/savList';
import { OperationEdit } from './operations/operationEdit';
import { SAVEdit } from './operations/savEdit';
import { TabbedPartnersBillList } from './partners_bill/partnersBillList';
import { PartnersBillEdit, PartnersBillCreate } from './partners_bill/partnersBillEdit';
import { TabbedClientsBillList } from './clients_bill/clientsBillList';
import { ClientsBillEdit, ClientsBillCreate } from './clients_bill/clientsBillEdit';

import { RefurbisherList } from './refurbisher/refurbisherList';
import { RefurbisherEdit, RefurbisherCreate } from './refurbisher/refurbisherEdit';
import 'fontsource-roboto';



import { EquipmentList } from './equipments/equipmentList';
//import jsonServerProvider from 'ra-data-json-server';

const dataProvider = customDataProvider('https://api.airtable.com/v0/app7dXu5oLwGP1OX5');
//const dataProvider = jsonServerProvider('https://jsonplaceholder.typicode.com');

const theme = createMuiTheme({
  typography: {
    //fontFamily: 'Roboto',
    h6: {
      fontSize: '1.1rem',
      fontWeight: '800',
    },
  },
    palette: {
      primary: {
        main: '#F9BC60',
        contrastText: '#fff'
      },
      secondary: {
        light: '#2660A4',
        main: '#2660A4',
        dark: '#192D4B',
      },
    },
});

const App = () => (
  <Admin dataProvider={dataProvider} theme={theme} layout={CustomLayout} >
    <Resource name="listes" options={{ label: 'Demandes' }} list={TabbedListList} edit={ListeEdit} create={ListCreate} />
    <Resource name="clients" options={{ label: 'Clients' }} list={CustomClientList} edit={ClientEdit}  />
    <Resource name="machines_desirees" options={{ label: 'Machines dsires' }} list={ListGuesser} edit={EditGuesser}/>
    <Resource name="vesto_stock" options={{ label: 'Vesto Stock' }} list={EquipmentList}/>
    <Resource name="categories" list={ListGuesser} edit={EditGuesser} />
    <Resource name="retraitement_machines" list={TabbedEquipmentProcessingList} edit={EquipmentProcessingEdit} create={EquipmentProcessingCreate}/>
    <Resource name="operations" list={TabbedOperationsList} edit={OperationEdit} />
    <Resource name="sav" list={TabbedSAVList} edit={SAVEdit} />
    <Resource name="factures" list={TabbedPartnersBillList} edit={PartnersBillEdit} create={PartnersBillCreate}/>
    <Resource name="reconditionneurs" list={RefurbisherList} edit={RefurbisherEdit} create={RefurbisherCreate} />
    <Resource name="comments" />
    <Resource name="services"/>
    <Resource name="factures_clients" list={TabbedClientsBillList} edit={ClientsBillEdit} create={ClientsBillCreate}/>
  </Admin>
);

export default App
