
import React, { useState } from 'react';
import {
    Button,
    SaveButton,
    useRefresh
} from 'react-admin';
import customDataProvider from '../dataProvider';

import IconContentAdd from '@material-ui/icons/Add';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import { EquipmentList } from '../equipments/equipmentList';
import IconCancel from '@material-ui/icons/Cancel';
import { listTransform } from '../operations/operationEdit';


const dataProvider = customDataProvider('https://api.airtable.com/v0/app7dXu5oLwGP1OX5');


export const EquipmentQuickAddButton = (props) => {
    const [showDialog, setShowDialog] = useState(false);
    const [equipmentId, setEquipmentId] = useState();
    const refresh = useRefresh();

    const handleClick = () => {
        setShowDialog(true);
    };

    const handleCloseClick = () => {
        setShowDialog(false);
        setEquipmentId('');
    };

    const handleSelectEquiment = id => {
        console.log('in handle select equipment', id)
        setEquipmentId(id)
    }

    const handleSubmit = async () => {
        console.log('var', props.var, props)
        if (props.var === 'proforma') {
            const params = { id: props.record.id, data: listTransform({...props.record, 'Machines proforma': props.record['Machines proforma'] ? props.record['Machines proforma'].concat([equipmentId]) : [equipmentId] })}
            await dataProvider.update('listes', params)
        } else {
            const params = { id: props.record.id, data: {'Dtails': props.record['Dtails'], 'Type de machine': props.record['Type de machine'], 'Client': props.record['Client'], 'Mach. possibles': props.record['Mach. possibles'] ? props.record['Mach. possibles'].concat([equipmentId]): [equipmentId]}}
            await dataProvider.update('machines_desirees', params)
        }
        setShowDialog(false);
        setEquipmentId('');
        refresh();
    }
    return (
        <>
            <Button onClick={handleClick} >
                <IconContentAdd />
            </Button>
            <Dialog
                fullWidth
                maxWidth='xl'
                open={showDialog}
                onClose={handleCloseClick}
            >
                <DialogTitle>Ajouter une machine possible</DialogTitle>
                    <DialogContent>
                        <EquipmentList
                            basePath='/vesto_stock'
                            resource='vesto_stock'
                            variant='select'
                            selectedId=''
                            handleClick={handleSelectEquiment}/>
                    </DialogContent>
                    <DialogActions>
                        <Button
                            label="ra.action.cancel"
                            onClick={handleCloseClick}
                            //disabled={loading}
                        >
                            <IconCancel />
                        </Button>
                        <SaveButton
                            handleSubmitWithRedirect={
                                handleSubmit
                            }
                            //pristine={pristine}
                            //saving={saving}
                            disabled={!equipmentId}
                        />
                   </DialogActions>
            </Dialog>
        </>
    );

}