import { Datagrid, TextField, ReferenceField, DateField, useListContext, ListContextProvider, ListBase, Pagination, TopToolbar, ExportButton } from 'react-admin';
import { useCallback } from 'react';
import { Card, CardContent, Grid, Tabs, Tab } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';


const useStyles = makeStyles(theme => ({
    root: {
        maxWidth: '500px',
    },
}))

export const SAVStatusChoices = [
    {id: 'SAV: Informations client ncessaire'},
    {id: 'SAV: Client  recontacter'},
    {id: 'SAV: En attente de pice'},
    {id: 'SAV: Date de SAV  planifier'},
    {id: 'SAV: Date de SAV planifie'},
    {id: 'SAV: Rsolu'},
]

const tabs = [
    SAVStatusChoices
]

const TabbedDatagrid = (props) => {
    const listContext = useListContext();
    const { filterValues, setFilters, displayedFilters } = listContext;
    const handleChange = useCallback(
        (event, value) => {
            setFilters &&
                setFilters(
                    { ...filterValues, 'Statut intervention': value },
                    displayedFilters
                );
        },
        [displayedFilters, filterValues, setFilters]
    );
    const classes = useStyles();


    return (
        <Grid container spacing={4}>
            <Grid item xs={4}>
                {tabs.map(choices => (
                    <Grid item xs={12}>
                <Card>
                <CardContent>
                <Tabs
                    orientation="vertical"
                    centered
                    value={filterValues['Statut intervention']}
                    indicatorColor="primary"
                    textColor="primary"
                    onChange={handleChange}
                >
                    {choices.map(choice => (
                        <Tab
                            key={choice.id}
                            label={choice.id}
                            value={choice.id}
                            classes={classes}
                        />
                    ))}
                </Tabs>
                 </CardContent>
                 </Card>
                 <br/>
                 </Grid>
                 ))}
            </Grid>
            <Grid item xs={8}>
                <Card>
                    <CardContent>
                        <ListContextProvider
                            value={{ ...listContext }}
                        >
                            <Datagrid {...props} rowClick="edit">
                                <ReferenceField source="Client" reference="clients">
                                    <TextField source="Client" />
                                </ReferenceField>
                                <TextField source="Adresse installation" />
                                <DateField source="Debut prochaine intervention"/>
                            </Datagrid>
                            <Pagination />
                        </ListContextProvider>
                    </CardContent>
                 </Card>
            </Grid>
        </Grid>
    );
}

export const TabbedSAVList = props => {
    return(
    <ListBase
        {...props}
        filterDefaultValues={{ 'Statut intervention': 'Call de prparation  faire' }}
        perPage={10}
        
    >
        <TopToolbar >
            <ExportButton />
        </TopToolbar>
        <TabbedDatagrid />
    </ListBase>)
};