const dataProvider = customDataProvider('https://api.airtable.com/v0/app7dXu5oLwGP1OX5');


const StyledAccordion = withStyles({
    root: {
      width: '100%',
    },
  })(Accordion);


  export const ServiceDialog = (props) => {
    const [serviceId, setServiceId] = useState()
    const [ services, setServices ] = useState()

    const getServices = async () => {
        const services = await dataProvider.getList('services', {'pagination': {}, 'sort': {'field': 'Type', 'order': 'asc'}, 'filter': {'Statut': 'Livraison / Service'}})
        console.log(services, 'services')
        setServices(services )
    }
    useEffect(() => {
        getServices()
    }, [])
    const refresh = useRefresh();

    const handleSave = async () => {
        let params = {...listTransform(props.record), 'Livraison': props.record['Livraison'] ? props.record['Livraison'].concat([serviceId]) : [serviceId]}
        await dataProvider.update('listes', {id: props.record.id, data: params})
        refresh()
    }

    return(

    <Dialog
                        fullWidth
                        maxWidth='sm'
                        open={props.open}
                        onClose={() => props.setOpen(false)}
                    >
                        <DialogTitle>Ajouter un service</DialogTitle>
                        { services && services.data && <><DialogContent>
                            <Autocomplete
                                onChange={(event, newValue) => {setServiceId(newValue.id)}}
                                options={services.data}
                                getOptionLabel={(service) => `${service['Type']} - ${service['Prix HT']}`}
                                style={{ width: '100%' }}
                                renderInput={(params) => <ClassicalTextField {...params} variant="outlined" onChange={(event) => setServiceId(event.target.value)}/>}
                                />
                        </DialogContent>
                        <DialogActions>
                            <SaveButton
                                handleSubmitWithRedirect={
                                    () => {props.setOpen(false); handleSave()}
                                }
                                disabled={!serviceId}
                            />
                            <Button
                                label="ra.action.cancel"
                                onClick={() => props.setOpen(false)}
                            >
                                <IconCancel />
                            </Button>
                        </DialogActions></>}
                    </Dialog>)

}


