import { withStyles } from '@material-ui/core/styles';
import React from 'react';

import { Edit, BooleanInput, SimpleForm, TextInput, Create, Datagrid, RichTextField, ReferenceArrayField, DeleteButton } from 'react-admin';
import { Typography, Chip } from '@material-ui/core';

import { EquipmentCards } from '../equipments/equipmentCards';

import styled from 'styled-components';


const EquipmentDiv= styled.div`
    min-width: 300px;
    max-width: 400px;
    margin: 10px;
    .green {
        background-color: #3cb37170;
    }
    .red {
        background-color: #ff634770;
    }
    `;

const StyledCreate = withStyles({
    root: {
        width: '100%',
    },
  })(Create);



const wantedEquipmentTransform = (data) => {
    let newData = {
        'Type de machine': data['Type de machine'],
        'Dtails' : data['Dtails'],
        'Client' : data['Client'],
        'Mach. possibles': data['Mach. possibles'],
        'A rechercher': data['A rechercher']
    }
    return newData;
}

const wantedEquipmentCreateTransform = (data) => {
    let newData = {
        'Type de machine': data['Type de machine'],
        'Dtails' : data['Dtails'] || '<br/> ',
        'Client' : [data['id']],
        'A rechercher' : data['A rechercher'],
    }
    return newData;
}


const EquipmentDataGrid = (props) => {
    return(
        <ReferenceArrayField
            label="Machines possibles"
            source="Mach. possibles" //quel est le nom du champ
            reference="vesto_stock" // quel est le nom de la table / resource
            {...props}
        >
            <EquipmentCards {...props} editable={true} />
        </ReferenceArrayField>
    )
}

export const WantedEquipmentTitle = props => {
        return(
            <EquipmentDiv>
                <Typography variant='h6'>{props.record && props.record['Type de machine']}</Typography>
                <RichTextField source='Dtails'{...props}/>

                <Chip className={props.record && (props.record['A rechercher'] ? 'green' : 'red')} label={props.record && (props.record['A rechercher'] ? 'Recherch' : 'Non recherch')}/>
            </EquipmentDiv>
        )
}

export const WantedEquipmentList = (props) => {

    return(
        <>
    <Datagrid expand={WantedEquipmentEdit} {...props}>
        <WantedEquipmentTitle label='Machines dsires'/>
        <EquipmentDataGrid label='Machines proposes' />
        <DeleteButton redirect={false} label='' />
    </Datagrid>
    </>)
}

export const WantedEquipmentEdit = (props) => (
        <Edit transform={wantedEquipmentTransform} {...props}>
            <SimpleForm variant='outlined' redirect="" {...props}>
                <TextInput fullWidth source="Type de machine"/>
                <TextInput fullWidth multiline source="Dtails"/>
                <BooleanInput source='A rechercher'/>
            </SimpleForm>
        </Edit>
)

export const WantedEquipmentCreate = (props) => {
    return(
        <StyledCreate transform={wantedEquipmentCreateTransform} {...props} resource='machines_desirees'>
            <SimpleForm {...props} redirect={false} title=' ' variant='outlined'>
                <TextInput fullWidth source="Type de machine"/>
                <TextInput fullWidth multiline source="Dtails"/>
                <BooleanInput source='A rechercher'/>
            </SimpleForm>
        </StyledCreate>  
    )

}