import { Create, Edit, TextInput, SimpleForm, SelectInput } from 'react-admin';
import { makeStyles } from '@material-ui/core/styles';
import { Typography } from '@material-ui/core';

import { Comments } from '../comments/comments';




const useStyles = makeStyles(theme => ({
    inlineField: {
        display: 'inline-block',
        width: '25%',
        marginRight: '8%',
        minWidth: '200px',
    },
    inline6Field: {
        display: 'inline-block',
        width: '14%',
        marginRight: '3%',
        minWidth: '50px',
    },
    inlineHalfField: {
        display: 'inline-block',
        width: '45%',
        marginRight: '5%',
        minWidth: '300px',
    },
    inline23Field: {
        display: 'inline-block',
        width: '66%',
        minWidth: '200px',
    },
    fullWidth: {
        maxWidth: '80%',
    }
}))

const statusChoices = [
    {'id': 'Contact'},
    {'id': 'Rencontr'},
    {'id': '1ers dossiers'},
    {'id': 'Signature Charte'},
    {'id': 'Onboard'},
    {'id': 'Onboard sans Charte'},
]

const typeChoices = [
    {'id': 'Artisan / PME'},
    {'id': 'Industriel'},
    {'id': 'Fabricant'},
    {'id': 'Transporteur'},
    {'id': 'Autre'},
]

const activChoices = [
    {'id': 'Actif'},
    {'id': 'Non actif'},
    {'id': ''},
]

const mkpStatusChoices = [
    {'id': 'Veille'},
    {'id': 'A relancer'},
    {'id': 'Relanc'},
]

export const refurbisherTransform = (data) => {
    let newData = {...data}
    delete newData['id']
    return newData;
};

export const RefurbisherEdit = props => {
    const classes = useStyles();
    return(
    <Edit {...props} transform={refurbisherTransform} aside={<Comments/>} >
        <SimpleForm variant='outlined'>
            <TextInput source="Nom" formClassName={classes.inlineField}/>
            <br/>
            <Typography variant='h6'>Informations gnrales</Typography>
            <SelectInput source="Statut" choices={statusChoices} optionText='id' formClassName={classes.inlineField}/>
            <SelectInput source="Typologie" choices={typeChoices} optionText='id' formClassName={classes.inlineField}/>
            <TextInput source="Localisation" formClassName={classes.inlineField}/>
            <SelectInput source="Actif" choices={activChoices} optionText='id' formClassName={classes.inlineField}/>
            <TextInput source="Tlphone" formClassName={classes.inlineField}/>
            <br/>
            <Typography variant='h6'>Informations Marketplace</Typography>
            <SelectInput source='Statut de communication' choices={mkpStatusChoices} optionText='id' />
            <TextInput  fullWidth multiline source='Questions' />
        </SimpleForm>
    </Edit>
    )
};

export const RefurbisherCreate = props => {
    const classes = useStyles();
    return(
    <Create {...props} transform={refurbisherTransform} >
        <SimpleForm variant='outlined'>
            <TextInput source="Nom" formClassName={classes.inlineField}/>
            <br/>
            <Typography variant='h6'>Informations gnrales</Typography>
            <SelectInput source="Statut" choices={statusChoices} optionText='id' formClassName={classes.inlineField}/>
            <SelectInput source="Typologie" choices={typeChoices} optionText='id' formClassName={classes.inlineField}/>
            <TextInput source="Localisation" formClassName={classes.inlineField}/>
            <SelectInput source="Actif" choices={activChoices} optionText='id' formClassName={classes.inlineField}/>
            <TextInput source="Tlphone" formClassName={classes.inlineField}/>
            <br/>
            <Typography variant='h6'>Informations Marketplace</Typography>
            <SelectInput source='Statut de communication' choices={mkpStatusChoices} optionText='id' />
            <TextInput  fullWidth multiline source='Questions' />
        </SimpleForm>
    </Create>
    )
};