export const clientTransform = (data) => {
    let newData = data;

    //company fields
    delete data['Entreprise']
    delete data['Concept / Notes entreprise']
    delete data['Adresse']
    delete data['SIRET']

    // computed fields
    delete data['Fiche hubspot']
    delete data['id']
    delete data['Client']

    return newData
}

export const companyTransform = (data) => {
    let newData = data;

    //client fields
    delete data['Nom']
    delete data['Prnom']
    delete data['Tlphone']
    delete data['Source']
    delete data['email']
    delete data['Notes']

    // computed fields
    delete data['Fiche hubspot']
    delete data['id']
    delete data['Client']

    return newData
}

