const valdateRequestedProps = (statusNumber, data) => {
    let error = {}
    let isError = false
    for (var i = 1; i < statusNumber + 1; i++) {
        // eslint-disable-next-line no-loop-func
        requestedProps[i] && requestedProps[i].forEach(prop => {
            if (!data[prop]) {
                isError = true
                error[prop] = 'Obligatoire'
            }
        })  
    }
    return isError ? error : false
    
}

const getStatusNumber = (name) => {
    let number = 1
    statusChoices.forEach(status => {
        if (status.id === name) {
            number = status.number
        }
    })
    return number
}


export const ListeEdit = props => {
    const [error, setError] = useState(false)
    const notify = useNotify();

    const validateData = data => {
        setError(false)
        var validateError = valdateRequestedProps(getStatusNumber(data['Statut']), data)
        if (validateError) {
            setError(validateError)
            console.log('has error', validateError)
            let errorText = 'Il manque les informations suivantes pour passer au statut ' + data['Statut'] + ': '
            let errors = Object.keys(validateError)
            errors.forEach(error => {
                console.log(error)
                errorText += error + ', '
            })
            delete data['Statut']
            notify(errorText, 'warning', {}, false);
        }
        return(listTransform(data))
        
    }
    return(
    <Edit undoable={false} title={<ListTitle/>} aside={<Comments/>} transform={validateData} {...props} >
        <TabbedListEdit error={error}/>
    </Edit>
    )
};