import { Card, CardContent, Grid, Tabs, Tab } from '@material-ui/core';
import { Datagrid, DateField, TextField, FunctionField, ReferenceArrayField, ChipField, SingleFieldList, useListContext, ListContextProvider, ListBase, Pagination, TopToolbar, CreateButton, ExportButton } from 'react-admin';
import { useCallback } from 'react';
import { makeStyles } from '@material-ui/core/styles';


const useStyles = makeStyles(theme => ({
    root: {
        maxWidth: '500px',
    },
}))

export const billsStatus = [
    { id: 'A complter', name: 'A complter' },
    { id: 'En attente install', name: 'En attente install' },
    { id: 'Install finie - facture non reue', name: 'Install finie - facture non reue' },
    { id: 'Facture  rgler', name: 'Facture  rgler' },
    { id: 'Rgle', name: 'Rgle' },
];



const TabbedDatagrid = (props) => {
    const listContext = useListContext();
    const { filterValues, setFilters, displayedFilters } = listContext;
    const handleChange = useCallback(
        (event, value) => {
            setFilters &&
                setFilters(
                    { ...filterValues, 'Statut': value },
                    displayedFilters
                );
        },
        [displayedFilters, filterValues, setFilters]
    );
    const classes = useStyles();

    return (
        <Grid container spacing={4}>
            <Grid item xs={3}>
                <Grid item xs={12}>
                <Card>
                <CardContent>
                <Tabs
                    orientation="vertical"
                    centered
                    value={filterValues['Statut']}
                    indicatorColor="primary"
                    textColor="primary"
                    onChange={handleChange}
                >
                    {billsStatus.map(choice => (
                        <Tab
                            key={choice.id}
                            label={choice.name}
                            value={choice.name}
                            classes={classes}
                        />
                    ))}
                </Tabs>
                 </CardContent>
                 </Card>
                 <br/>
                 </Grid>
            </Grid>
            <Grid item xs={9}>
                <Card>
                    <CardContent>
                        <ListContextProvider
                            value={{ ...listContext }}
                        >
                            <Datagrid {...props} rowClick="edit">
                                <TextField source="Name" />
                                <FunctionField label="Valeur (HT)" render={record => record['Valeur (HT)'] ? `${record['Valeur (HT)']} ` : 'Non dfinie'} />
                                <ReferenceArrayField source="Acteur" reference="reconditionneurs">
                                    <SingleFieldList>
                                        <ChipField source='Nom'/>
                                    </SingleFieldList>
                                </ReferenceArrayField>
                                <ReferenceArrayField source="Client" reference="listes">
                                    <SingleFieldList>
                                        <ChipField source='Nom'/>
                                    </SingleFieldList>
                                </ReferenceArrayField>
                                <DateField source="Date chance" />

                            </Datagrid>
                            <Pagination />
                        </ListContextProvider>
                    </CardContent>
                 </Card>
            </Grid>
        </Grid>
    );
}

export const TabbedPartnersBillList = props => {
    return(
    <ListBase
        {...props}
        filterDefaultValues={{ 'Statut': 'Facture  rgler' }}
        perPage={10}
        
    >
        <TopToolbar>
            <CreateButton basePath="/factures" />
            <ExportButton />
        </TopToolbar>
        <TabbedDatagrid />
    </ListBase>)
};