import React, { useState } from 'react';

import { Card, CardContent, Typography, Grid, Chip, Drawer } from '@material-ui/core';

import { Price } from './equipmentList'

import { EquipmentQuickAddButton } from '../lists/EquipmentQuickAddButton'
import { EquipmentQuickDeleteButton } from '../lists/EquipmentQuickDeleteButton'

import { CardEquipmentEdit } from './equipmentEdit'
import { colorsForRefurbisher } from './equipmentList';
import { makeStyles } from '@material-ui/core/styles';


import styled from 'styled-components';

const useStyles = makeStyles(theme => ({
    inlineField: {
        width: '30%',
        marginRight: '3%',
        minWidth: 'fit-content',
    },
}))

const EquipmentDiv = styled.div`
    width: 400px;
    `;

const StyledCard = styled(Card)`
    :hover {
        cursor: pointer;
    }
    `;

const StyledCardContent = styled(CardContent)`
    :last-child {
        padding-bottom: 16px;
    }
    .blue {
        background-color: #4682b496;
    }
    .green {
        background-color: #3cb37170;
    }
    .violet {
        background-color: #7b68ee80;
    }
    .pink {
        background-color: #ee82ee80;
    }
    .red {
        background-color: #ff634770;
    }
    .grey {
        background-color: grey;
    }
    `;

const colorsForStatus = {
    'En attente de relecture': 'blue',
    'Rserv': 'violet',
    'Sur E-shop': 'green',
    'Disponible': 'green',
    'Vendu hors Vesto': 'red',
    'Vendu par Vesto': 'red',
    'Bloqu': 'pink',
}

export const EquipmentCards = (props) => {
    const [open, setOpen] = useState(false)
    const [openId, setOpenId] = useState()
    const [deleting, setDeleting] = useState(false)
    let editable = props.editable;
    const classes = useStyles()
    return (
        <>
            <EquipmentDiv><Grid container spacing={1} display="flex" flexDirection="column" justify='flex-start' >
                {props.ids.map(id => (
                    <>
                        <Grid item xs={12}>
                            <StyledCard onClick={() => { setOpen(true); setOpenId(id) }}>
                                <StyledCardContent>
                                    <Grid container spacing={3}>
                                        <Grid item xs={8}>
                                            <span style={{ 'font-weight': 'bold' }}>{props.data[id] && props.data[id]['Type']}</span>
                                            <Typography variant='body2'>{props.data[id] && props.data[id]['Marque']} - {props.data[id] && props.data[id]['Modele']}</Typography>
                                            <Typography><Price>{props.data[id] && props.data[id]['Prix HT']} </Price></Typography>
                                            <Chip className={`${props.data[id] && props.data[id]['Nom du reconditionneur (TECH)'] && colorsForRefurbisher[props.data[id]['Nom du reconditionneur (TECH)'][0]]} ${classes.inlineField}`} label={props.data[id] && props.data[id]['Nom du reconditionneur (TECH)'] && props.data[id]['Nom du reconditionneur (TECH)'][0]} />
                                            <Chip className={`${props.data[id] && colorsForStatus[props.data[id]['Statut']]} ${classes.inlineField}`} label={props.data[id] && props.data[id]['Statut']} />
                                            {editable && <EquipmentQuickDeleteButton var={props.var} setDeleting={setDeleting} equipmentId={id} record={props.record} />}
                                        </Grid>
                                        <Grid container item xs={4}>
                                            <div style={{ 'overflow': 'hidden', 'max-height': '130px' }}><img width='100%' alt={id} src={props.data[id] && props.data[id]['Photo'] && props.data[id]['Photo'][0]['thumbnails']['large']['url']} /></div>
                                        </Grid>
                                    </Grid>
                                </StyledCardContent>
                            </StyledCard>
                        </Grid>
                        <Drawer open={open && (openId === id) && !deleting} anchor="right" >
                            {id && <CardEquipmentEdit
                                recordId={id}
                                onCancel={() => setOpen(false)}
                            />}
                        </Drawer>
                    </>
                ))}
                {editable && <EquipmentQuickAddButton var={props.var} record={props.record} />}
            </Grid></EquipmentDiv>

        </>
    )
}