import * as React from "react";
import {
    List,
    Datagrid,
    TextField,
    EmailField,
    ChipField,
    Edit,
    SimpleForm,
    TextInput,
    ReferenceArrayField,
    SingleFieldList,
    SelectInput,
    Pagination
 } from 'react-admin';
 import { Typography } from '@material-ui/core';
 import { useStyles } from './lists/listEdit';
 import { sourceChoices } from './lists/generalFormTab';
 import { Comments } from './comments/comments';


const clientTransform = (data) => {
    let newData = data;


    // computed fields
    delete data['Fiche hubspot']
    delete data['id']
    delete data['Client']

    return newData
}

const ClientPagination = props => <Pagination rowsPerPageOptions={[10, 25, 50, 100]} {...props} />;


export const CustomClientList = props => (
    <List {...props} perPage={100} pagination={<ClientPagination />} actions={false}>
        <Datagrid rowClick="edit">
            <TextField source="Client" />
            <TextField source="Entreprise" />
            <EmailField source="email" />
            <TextField source="Tlphone" />
            {/* <ReferenceArrayField source="Listes" label='Demandes' reference="listes">
                <SingleFieldList>
                    <ChipField source='Nom'/>
                </SingleFieldList>
            </ReferenceArrayField> */}
        </Datagrid>
    </List>
);

export const ClientEdit = props => {
    const classes = useStyles()
    return(
    <Edit {...props} aside={<Comments/>} transform={clientTransform}>
        <SimpleForm>
            <br /><Typography variant='h6'>Infos Client</Typography>
            <TextInput variant='outlined' formClassName={classes.inlineField} fullWidth source='Nom' />
            <TextInput variant='outlined' formClassName={classes.inlineField} fullWidth source='Prnom' />
            <TextInput variant='outlined' formClassName={classes.inlineField} fullWidth source='Tlphone' />
            <TextInput variant='outlined' formClassName={classes.inline23Field} fullWidth source='email' />
            <SelectInput choices={sourceChoices} optionText='id' variant='outlined' formClassName={classes.inlineField} fullWidth source='Source' />
            <TextInput variant='outlined' multiline formClassName={classes.fullWidth} fullWidth source='Notes' />
            <br /><Typography variant='h6'>Infos Entreprise</Typography>
            <TextInput variant='outlined' formClassName={classes.inlineField} fullWidth source='Entreprise' />
            <TextInput variant='outlined' formClassName={classes.inlineField} fullWidth source='Adresse' />
            <TextInput variant='outlined' formClassName={classes.inlineField} fullWidth label='SIRET' source='SIRET' />
            <TextInput variant='outlined' multiline formClassName={classes.fullWidth} fullWidth source='Concept / Notes entreprise' />
            <br /><Typography variant='h6'>Toutes les demandes du client</Typography>
                <ReferenceArrayField fullWidth source='Listes' reference='listes'>
                    <SingleFieldList>
                        <ChipField source='Nom' />
                    </SingleFieldList>
                </ReferenceArrayField>
        </SimpleForm>
    </Edit>)
};