import { NumberInput, FileField, SimpleForm, DateInput, Edit, required, TextInput, SelectInput, Create, AutocompleteArrayInput, ReferenceArrayField, ChipField, SingleFieldList } from 'react-admin';
import { Button, Typography } from '@material-ui/core';
import { useDataProvider, ReferenceArrayInput, AutocompleteInput } from 'react-admin';
import { billsStatus } from './partnersBillList';
import { useStyles } from '../lists/listEdit';
import { useState, useEffect } from 'react'
import { Comments } from '../comments/comments';


const PartnersBillEditForm = (props) => {
    const classes = useStyles()
    return(
        <SimpleForm {...props} variant='outlined'>
                <SelectInput source="Statut" choices={billsStatus} formClassName={classes.inlineField} fullWidth/>
                <DateInput source="Date chance" formClassName={classes.inlineField} fullWidth/>
                <NumberInput source='Valeur (HT)' label='Valeur (HT)' />
                <ReferenceArrayField formClassName={classes.inlineField} source="Acteur" reference="reconditionneurs">
                    <SingleFieldList>
                        <ChipField source='Nom'/>
                    </SingleFieldList>
                </ReferenceArrayField>
                <ReferenceArrayField formClassName={classes.inlineField} source="Client" reference="listes">
                    <SingleFieldList>
                        <ChipField source='Nom'/>
                    </SingleFieldList>
                </ReferenceArrayField>
                <br/>
                <TextInput formClassName={classes.fullWidth} multiline fullWidth source="Notes" />
                <br/>
                <FileField source="Facture" src='url' title='filename' target="_blank"  fullWidth/>
                <Button href={`https://airtable.com/shryQIshaRPxDIE8L?prefill_Facture%20reconditionneur=${props.record.id}`} formClassName={classes.inline23Field} variant='contained' color='secondary' target='_blank' >Ajouter une facture</Button>
        </SimpleForm>
    )
}
export const PartnersBillEdit = props => {
    return(
        <Edit {...props} aside={<Comments/>}>
            <PartnersBillEditForm />
        </Edit>
    )
};

export const PartnersBillCreate = (props) => {
    const classes = useStyles()
    const dataProvider = useDataProvider()
    const [ clients, setClients ] = useState()
    const [ refurbishers, setRefurbishers ] = useState()

    const getClients = async () => {
        const clients = await dataProvider.getList('listes', {'pagination': {'perPage': 1000}, 'sort': {'field': 'id', 'order': 'asc'}, 'filter': {}})
        setClients(clients)
        return clients
    }

    const getRefurbishers = async () => {
        const refurbishers = await dataProvider.getList('reconditionneurs', {'pagination': {'perPage': 1000}, 'sort': {'field': 'id', 'order': 'asc'}, 'filter': {}})
        setRefurbishers(refurbishers)
        return refurbishers
    }

    useEffect(() => {
        getClients()
        getRefurbishers()
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    const transform = (data) => {
        let newData = data
        if (data['Acteur']) {
            newData['Acteur'] = [data['Acteur']]
        }
        return(newData)
    }

    return(
    <Create {...props} transform={transform}>
        <SimpleForm variant='outlined'>
            <Typography variant='h6'>Nouvelle facture</Typography>
                <SelectInput source="Statut" choices={billsStatus} formClassName={classes.inlineField} fullWidth validate={required()}/>
                <NumberInput source="Valeur (HT)" label='Valeur (HT)' formClassName={classes.inlineField} fullWidth validate={required()}/>

                <DateInput source="Date chance" formClassName={classes.inlineField} fullWidth/>
                { refurbishers && <ReferenceArrayInput  formClassName={classes.inline23Field} fullWidth source='Acteur' reference='reconditionneurs' >
                    <AutocompleteInput setFilter={() => false} choices={refurbishers.data ? refurbishers.data : []} optionText={(record) => (`${(record && record['Nom']) || ''}`)} />
                </ReferenceArrayInput>}
                { clients && <ReferenceArrayInput formClassName={classes.inline23Field} fullWidth source='Client' label='Demande client' reference='listes'>
                    <AutocompleteArrayInput setFilter={() => false} choices={clients.data || []} optionText={(record) => (`${(record && record['Nom']) || ''}`)} />
                </ReferenceArrayInput>}
                <br/>
                <TextInput multiline formClassName={classes.fullWidth} fullWidth source="Notes" />
        </SimpleForm>
    </Create>
    )
}