import React from 'react';
import { TextField, useNotify, DateTimeInput, Edit, TextInput, ReferenceField, SimpleForm, SelectInput, ReferenceArrayField } from 'react-admin';
import { longTermStatusChoices, shortTermStatusChoices } from './operationsList';
import { SAVStatusChoices } from './savList';
import { EquipmentCards } from '../equipments/equipmentCards';
import { makeStyles } from '@material-ui/core/styles';
import { ClientDetails } from '../lists/listEdit';
import {  Typography } from '@material-ui/core';
import { users } from '../users';


import { Comments } from '../comments/comments';



const useStyles = makeStyles(theme => ({
    inlineField: {
        display: 'inline-block',
        width: '25%',
        marginRight: '8%',
        minWidth: '200px',
    },
    inline6Field: {
        display: 'inline-block',
        width: '14%',
        marginRight: '3%',
        minWidth: '50px',
    },
    inlineHalfField: {
        display: 'inline-block',
        width: '45%',
        marginRight: '5%',
        minWidth: '300px',
    },
    inline23Field: {
        display: 'inline-block',
        width: '66%',
        minWidth: '200px',
    },
    fullWidth: {
        maxWidth: '80%',
    }
}))


const statutsChoices = longTermStatusChoices.concat(shortTermStatusChoices).concat(SAVStatusChoices)

export const listTransform = (data) => {
    let newData = {...data}
    delete newData['Numro de Liste']
    delete newData['Nom par dfaut']
    delete newData['Date de cration']
    delete newData['Nb machines']
    delete newData['Fiche hubspot']
    delete newData['Crer devis']
    delete newData['Crer proforma']
    delete newData['id']
    delete newData['Entreprise']
    delete newData['Prnom client (auto)']
    delete newData['Nom client (auto)']
    delete newData['Lien hubspot']
    delete newData['Machines devis']
    delete newData['Reconditionneurs impliqus']
    delete newData['Total services']
    delete newData['Total machines']
    delete newData['Total']
    delete newData['Adresse client']
    if (newData['Owner name']) {
        let id = ''
        users.forEach(userChoice => {
            if (userChoice.name === newData['Owner name']) {
                id = userChoice.id
            }
        })
        newData['Owner'] = [{'id': id}]
    }
    delete newData['Owner name']
    delete newData['Temps depuis installation (mois)']
    
    return newData;
};

export const OperationEdit = props => {
    const classes = useStyles();
    const notify = useNotify();

    const validateData = (data) => {
        if (data['Statut intervention'] === 'Date install planifie' ) {
            if (!(data['Debut installation'] && data['Fin installation'])) {
                data['Statut intervention'] = 'Date install  planifier';
                notify('Les dates d\'installation ne sont pas renseignes', 'warning', {}, false);
            }
        }
        else if (data['Statut intervention'] === 'Termin' ) {
            if (!data['CR pour les Sales']) {
                data['Statut intervention'] = 'Date install planifie';
                notify('Il manque le CR pour les sales', 'warning', {}, false);
            }
        }
        return listTransform(data)
    }
    return(
    <Edit {...props} transform={validateData} undoable={false} aside={<Comments/>}>
        <SimpleForm variant='outlined' warnWhenUnsavedChanges redirect={false} >
            <TextField source="Nom" formClassName={classes.inlineField} />
            <SelectInput optionText='id' formClassName={classes.inlineField} choices={statutsChoices} source="Statut intervention" />
            <ReferenceField source="Client" reference="clients" formClassName={classes.inlineField} >
                <ClientDetails />
            </ReferenceField>
            
            <TextInput multiline fullWidth label='Notes installs / SAV' source="Notes installs / SAV" formClassName={classes.fullWidth}/>
            <br/>
            <Typography variant='h6'>Informations install</Typography><br/>
            <TextInput source="Adresse installation" formClassName={classes.fullWidth} fullWidth/>
            <TextInput source="Acces" fullWidth formClassName={classes.inlineHalfField}/>
            <TextInput source="Souhait de reprise" formClassName={classes.inlineHalfField} fullWidth/>
            <TextInput source="Priode installation souhaite" formClassName={classes.inlineField}/>
            <TextInput source="Urgence et indisponibilits" formClassName={classes.inline23Field}/>

            <DateTimeInput source="Debut installation" formClassName={classes.inlineField}/>
            <DateTimeInput source="Fin installation" formClassName={classes.inlineField}/>
            <TextInput multiline fullWidth label='CR pour les sales' source='CR pour les Sales' formClassName={classes.fullWidth}/>
        {/*<NumberInput source='Frais supplmentaires' formClassName={classes.inlineField}/>
            <TextInput source='Notes frais' label='Notes'  formClassName={classes.inline23Field}/>*/}
            <br/>
            <Typography variant='h6'>Machines</Typography><br/>
            <ReferenceArrayField source="Machines devis" reference="vesto_stock">
                <EquipmentCards editable={false} />
            </ReferenceArrayField>
        </SimpleForm>
    </Edit>
)};