import * as React from "react";
import { Fragment, useCallback, useState } from 'react';
import { Datagrid, Filter, TextField, ReferenceField, ArrayField, TextInput, SelectInput, CloneButton, ChipField, SingleFieldList, useListContext, ListContextProvider, ListBase, Pagination, TopToolbar, CreateButton, ExportButton } from 'react-admin';
import { Card, CardContent, Grid, Button, Tabs, Tab, Menu, MenuItem } from '@material-ui/core';
import { users } from '../users';
import { makeStyles } from '@material-ui/core/styles';


import PersonIcon from '@material-ui/icons/Person';


const useStyles = makeStyles(theme => ({
    root: {
        maxWidth: '500px',
    },
}))


export const ListTitle = ({ record }) => {
        return <span>Liste {record ? record['Nom'] : ''}</span>;
    };

const OwnerFilter = (props) => (
    <Filter {...props} context="button">
        <TextInput label="Search" source="q" alwaysOn />
        <SelectInput label="Owner" source="owner" choices={users} alwaysOn/>
        <TextInput label="Title" source="title" defaultValue="Hello, World!" alwaysOn/>
    </Filter>
    );


const preSalesStatus = [
    { id: '1. A attribuer', name: '1. A attribuer', number: 1 },
    { id: '2. Affinage du besoin', name: '2. Affinage du besoin', number: 2 },
    { id: '3. Proforma  recontacter', name: '3. Proforma  recontacter', number: 3 },
];

const salesStatus = [
    { id: '4. Devis  envoyer', name: '4. Devis  envoyer', number: 4 },
    { id: '5. Devis envoy', name: '5. Devis envoy', number: 5 },
    { id: '6. Devis en ngo', name: '6. Devis en ngo', number: 6 },
    { id: '7. Devis sign', name: '7. Devis sign', number: 7 },
    { id: '8. Pass aux ops', name: '8. Pass aux ops', number: 8 },
];

const afterSalesStatus = [
    { id: '9. Installation termine', name: '9. Installation termine', number: 9 },
    { id: 'Gagn (sous garantie)', name: 'Gagn (sous garantie)', number: 10 },
    { id: 'Gagn (hors garantie)', name: 'Gagn (hors garantie)', number: 11 },
    { id: 'Perdu', name: 'Perdu', number: 12 },
];

export const statusChoices = preSalesStatus.concat(salesStatus).concat(afterSalesStatus)

const tabs = [preSalesStatus, salesStatus, afterSalesStatus]

const TabbedDatagrid = (props) => {
    const listContext = useListContext();
    const { filterValues, setFilters, displayedFilters } = listContext;
    const handleChange = useCallback(
        (event, value) => {
            setFilters &&
                setFilters(
                    { ...filterValues, 'Statut': value },
                    displayedFilters
                );
        },
        [displayedFilters, filterValues, setFilters]
    );
    const classes = useStyles();

    return (
        <Grid container spacing={4}>
            <Grid item xs={3}>
            {tabs.map(choices => (
                <Grid item xs={12}>
                <Card>
                <CardContent>
                <Tabs
                    orientation="vertical"
                    centered
                    value={filterValues['Statut']}
                    indicatorColor="primary"
                    textColor="primary"
                    onChange={handleChange}
                >
                    {choices.map(choice => (
                        <Tab
                            key={choice.id}
                            label={choice.name}
                            value={choice.name}
                            classes={classes}
                        />
                    ))}
                </Tabs>
                 </CardContent>
                 </Card>
                 <br/>
                 </Grid> ))}
            </Grid>
            <Grid item xs={9}>
                <Card>
                    <CardContent>
                        <ListContextProvider
                            value={{ ...listContext }}
                        >
                            <Datagrid {...props} rowClick="edit">
                                <TextField source="Nom" />
                                <ReferenceField source="Client" reference="clients">
                                <TextField source="Client" />
                                </ReferenceField>
                                <ArrayField source="Owner"><SingleFieldList><ChipField source="name" /></SingleFieldList></ArrayField>
                                <TextField source="Priode installation souhaite" />
                                <CloneButton />
                            </Datagrid>
                            <Pagination />
                        </ListContextProvider>
                    </CardContent>
                 </Card>
            </Grid>
        </Grid>
    );
}

const OwnerButton = props => {
    const { setFilters, filterValues, displayedFilters } = useListContext();
    const [open, setOpen] = useState(false)
    const [label, setLabel] = useState(filterValues['owner'] ? filterValues['owner'] : 'Owner')
    const [anchorEl, setAnchorEl] = React.useState(null);

    const handleOwnerClick = (event) => {
        setOpen(true)
        setAnchorEl(event.currentTarget);
    }

    const handleChangeSort = (owner) => {
        let newFilters = filterValues
        if ('owner' in filterValues && filterValues['owner'] ===  owner['name']) {
            delete newFilters['owner']
            setLabel('Owner')
        } else {
            newFilters['owner'] = owner['name']
            setLabel(owner['name'])
        }
        setFilters(newFilters, displayedFilters);
        setOpen(false)
    }

    return(
        <>
            <Button
                onClick={handleOwnerClick}
                label='test'
                color="primary"
                startIcon={<PersonIcon/>}
                size="small">
                {label}    
            </Button>
            <Menu
                open={open}
                onClose={() => setOpen(false)}
                anchorEl={anchorEl}>
                    {users.map(owner => (
                        <MenuItem
                            onClick={() => handleChangeSort(owner)}
                            data-sort={owner}
                            key={owner.id}
                        >
                            {owner.name}
                        </MenuItem>
                    ))}
            </Menu>
        </>

    )

}

export const TabbedListList = props => {
    return(
    <ListBase
        {...props}
        filterDefaultValues={{ 'Statut': '2. Affinage du besoin' }}
        perPage={10}
        
    >
        <TopToolbar filters={<OwnerFilter/>}>
            <OwnerButton />
            <CreateButton basePath="/listes" />
            <ExportButton />
        </TopToolbar>
        <TabbedDatagrid />
    </ListBase>)
};
