import { Card, CardContent, Grid, Tabs, Tab } from '@material-ui/core';
import { Datagrid, BooleanField, TextField, FunctionField, ReferenceArrayField, ChipField, SingleFieldList, useListContext, ListContextProvider, ListBase, Pagination, TopToolbar, CreateButton, ExportButton } from 'react-admin';
import { useCallback } from 'react';
import { makeStyles } from '@material-ui/core/styles';


const useStyles = makeStyles(theme => ({
    root: {
        maxWidth: '500px',
    },
}))

export const billsStatus = [
    { id: 'Devis sign', name: 'Devis sign' },
    { id: 'Install cette semaine', name: 'Install cette semaine' },
    { id: 'A envoyer', name: 'A envoyer' },
    { id: 'Envoy', name: 'Envoy' },
    { id: 'A relancer', name: 'A relancer' },
    { id: 'Rgl', name: 'Rgl' },
];



const TabbedDatagrid = (props) => {
    const listContext = useListContext();
    const { filterValues, setFilters, displayedFilters } = listContext;
    const handleChange = useCallback(
        (event, value) => {
            setFilters &&
                setFilters(
                    { ...filterValues, 'Statut': value },
                    displayedFilters
                );
        },
        [displayedFilters, filterValues, setFilters]
    );
    const classes = useStyles();

    return (
        <Grid container spacing={4}>
            <Grid item xs={3}>
                <Grid item xs={12}>
                <Card>
                <CardContent>
                <Tabs
                    orientation="vertical"
                    centered
                    value={filterValues['Statut']}
                    indicatorColor="primary"
                    textColor="primary"
                    onChange={handleChange}
                >
                    {billsStatus.map(choice => (
                        <Tab
                            key={choice.id}
                            label={choice.name}
                            value={choice.name}
                            classes={classes}
                        />
                    ))}
                </Tabs>
                 </CardContent>
                 </Card>
                 <br/>
                 </Grid>
            </Grid>
            <Grid item xs={9}>
                <Card>
                    <CardContent>
                        <ListContextProvider
                            value={{ ...listContext }}
                        >
                            <Datagrid {...props} rowClick="edit">
                                <TextField source="Nom" />
                                <FunctionField label="Total (HT)" render={record => record['Total (HT)'] ? `${record['Total (HT)']} ` : 'Non dfini'} />
                                <ReferenceArrayField source="Client" reference="listes">
                                    <SingleFieldList>
                                        <ChipField source='Nom'/>
                                    </SingleFieldList>
                                </ReferenceArrayField>
                                <FunctionField label="Acompte (TTC)" render={record => `${Math.round((record['Total (HT)'] || 0) * 1.2 * 0.3 * 100)/100} `} />
                                <BooleanField source='Acompte pay'/>
                            </Datagrid>
                            <Pagination />
                        </ListContextProvider>
                    </CardContent>
                 </Card>
            </Grid>
        </Grid>
    );
}

export const TabbedClientsBillList = props => {
    return(
    <ListBase
        {...props}
        filterDefaultValues={{ 'Statut': 'Install cette semaine' }}
        perPage={10}
        
    >
        <TopToolbar>
            <CreateButton basePath="/factures_clients" />
            <ExportButton />
        </TopToolbar>
        <TabbedDatagrid />
    </ListBase>)
};