export const equipmentTransform = (data) => {
    let newData = {...data}
    delete newData['ID Bubble']
    delete newData['Ajouter au E-shop']
    delete newData['Annonce']
    delete newData['Localisation']
    delete newData['Nom du reconditionneur (TECH)']
    delete newData['Nom client propos (TECH)']
    delete newData['Catgorie']
    delete newData['Date de cration']
    delete newData['Marque - Modle']
    delete newData['id']
    delete newData['Catgorie']
    delete newData['Fiches techniques']
    delete newData['Today']
    newData['Fiche technique'] = newData['Fiche technique'] && newData['Fiche technique'].map(attachment => {
        delete attachment.rawFile;
        return(attachment)
    })
    if (newData['Owner name']) {
        let id = ''
        users.forEach(userChoice => {
            if (userChoice.name === newData['Owner name']) {
                id = userChoice.id
            }
        })
        newData['Owner'] = [{'id': id}]
    }
    delete newData['Owner name']
    return newData;
};


