export const CardEquipmentEdit = props => {
    let response = useGetOne('vesto_stock', props.recordId)
    const [changed, setChanged] = useState(false, [])
    const classes = useStyles();
    let newProps = {...props}
    newProps.id = props.recordId
    newProps.basePath = '/vesto_stock'
    newProps.resource = 'vesto_stock'
    newProps.transform = equipmentTransform
    newProps.undoable = false
    newProps.redirect = false
    let controllerProps = useEditController(newProps)
    const notify = useNotify()

    const handleClose = () => {
        if (changed) {
            notify('Changements non sauvegards', 'warning')
        } else {
            props.onCancel()
        }
    }

    return (
        <StyledDiv>
            <div className={classes.title}>
                <Typography variant="h6">
                    {response && response.data && response.data['Type']}
                </Typography>
                <IconButton onClick={handleClose}>
                    <CloseIcon />
                </IconButton>
            </div>
            {response && response.data && 
            <EditContextProvider value={controllerProps} >
                <EditView {...newProps} {...controllerProps} className={classes.edit} aside={<Comments/>} >
                    <EquipmentForm setChanged={setChanged}/>
                </EditView>
            </EditContextProvider>
            
            }
        </StyledDiv>
    )
};

export const EquipmentEdit = props => {
    const classes = useStyles();
    const [changed, setChanged] = useState(false)
    const notify = useNotify()
    let response = useGetOne('vesto_stock', props.recordId)

    const handleClose = () => {
        if (changed) {
            notify('Changements non sauvegards', 'warning')
        } else {
            props.onCancel()
        }
    }

    return (
        <StyledDiv>
            <div className={classes.title}>
                <Typography variant="h6">
                    {response.data && response.data['Type']}
                </Typography>
                <IconButton onClick={handleClose}>
                    <CloseIcon />
                </IconButton>
            </div>
            <Edit {...props} className={classes.edit} aside={<Comments/>} undoable={false} transform={equipmentTransform}>
                <EquipmentForm setChanged={setChanged}/>
            </Edit>
        </StyledDiv>
    )
};