import { Typography, Grid } from '@material-ui/core';
import { ReferenceField, SelectInput, FormWithRedirect, ReferenceArrayField, FormTab, TextInput, SingleFieldList, ChipField } from 'react-admin';
import { useStyles } from './listEdit';
import { statusChoices } from './listesList';
import { users } from '../users';
import { useFormState } from 'react-final-form';


export const sourceChoices = [
    { 'id': 'Rseaux sociaux' },
    { 'id': 'Bouche  oreille' },
    { 'id': 'Google' },
    { 'id': 'Facebook' },
    { 'id': 'Le bon coin' },
    { 'id': 'Chasse' },
    { 'id': 'Groupement restaurateurs (ecotable)' },
    { 'id': 'Autre' },

]

export const ClientForm = (props) => {
    let { values } = useFormState();
    const classes = useStyles()
    props.setClientProps(values)

    return (
        <Grid {...props} container spacing={0}>
            <TextInput variant='outlined' className={classes.inlineField} fullWidth source='Nom' />
            <TextInput variant='outlined' className={classes.inlineField} fullWidth source='Prnom' />
            <TextInput variant='outlined' className={classes.inlineField} fullWidth source='Tlphone' />
            <TextInput variant='outlined' className={classes.inline23Field} fullWidth source='email' />
            <SelectInput choices={sourceChoices} optionText='id' variant='outlined' className={classes.inlineField} fullWidth source='Source' />
            <TextInput variant='outlined' multiline className={classes.fullWidth} fullWidth source='Notes' />
        </Grid>
    )
}

export const CompanyForm = (props) => {
    let { values } = useFormState();
    const classes = useStyles()
    props.setCompanyProps(values)

    return (
        <Grid {...props} container spacing={0}>
            <TextInput variant='outlined' className={classes.inlineField} fullWidth source='Entreprise' />
            <TextInput variant='outlined' className={classes.inlineField} fullWidth source='Adresse' />
            <TextInput variant='outlined' className={classes.inlineField} fullWidth label='SIRET' source='SIRET' />
            <TextInput variant='outlined' multiline className={classes.fullWidth} fullWidth source='Concept / Notes entreprise' />
        </Grid>)
}


export const GeneralFormTab = props => {
    const classes = useStyles()
    return (
        <FormTab
            label="Gnral"
            contentClassName={''}
            variant='outlined'
            {...props}
        >
            <TextInput source='Nom' formClassName={classes.fullWidth} fullWidth />
            <SelectInput source="Statut" formClassName={classes.inlineHalfField} optionText='id' choices={statusChoices} />
            <SelectInput error={props.error && props.error['Owner']} helperText={props.error && props.error['Owner']} formClassName={classes.inlineHalfField} source="Owner name" label='Owner' optionText='name' optionValue='name' choices={users} />
            <br /><Typography variant='h6'>Infos Client</Typography>
            <ReferenceField link={false} fullWidth source='Client[0]' label='' reference='clients'>
                <FormWithRedirect {...props} setClientProps={props.setClientProps} render={formProps => <ClientForm {...formProps} />} />
            </ReferenceField>
            <br /><Typography variant='h6'>Infos Entreprise</Typography>
            <ReferenceField link={false} fullWidth source='Client[0]' label='' reference='clients'>
                <FormWithRedirect {...props} setCompanyProps={props.setCompanyProps} save={(data) => console.log(data)} render={formProps => <CompanyForm {...formProps} />} />
            </ReferenceField>

            <Typography variant='h6'>Toutes les demandes du client</Typography>
            <ReferenceField link={false} fullWidth source='Client[0]' label='' reference='clients'>
                <ReferenceArrayField source='Listes' reference='listes'>
                    <SingleFieldList>
                        <ChipField source='Nom' />
                    </SingleFieldList>
                </ReferenceArrayField>
            </ReferenceField>
        </FormTab>
    )
}